import React, { useState } from "react";

function App() {
  const [text, setText] = useState("");
  const [task, setTask] = useState([]);

  const handler = (e) => {
    setText(e.target.value);
  };
  const submited = (e) => {
    e.preventDefault();
    setTask([text, ...task]);
    setText("");
  };
  return (
    <>
      <div>
        <input type="text" value={text} onChange={handler} />
        <button onClick={submited}>Add</button>
      </div>
      <div>
        {task.map((entry, index) => {
          return (
            <>
              <h4>{entry}</h4>
              <button
                onClick={() => {
                  removeTask(index);
                }}
              >
                Delete
              </button>
            </>
          );
        })}
      </div>
    </>
  );
}
export default App;
